<template>
<div id="box">
  <wu/>
<div>
  <p>当前定位城市<span style="float:right">定位不准时，请在城市列表中选择</span></p>
  <p><a href="http://localhost:8080/#/wu" @click="faA()">{{list}}<span class="el-icon-arrow-right"style="float:right"></span></a></p>
</div>
<div>
    <el-radio-group v-for="item in arr" style='width:25%'>
      <el-radio-button :label='item.name'></el-radio-button>
    </el-radio-group>
  </div>
  <div>
    <div v-for="(item,key) in Arr" class="xun" :key='key'>
         <p style="width:100%">{{item}}</p>
      <P v-for="aa in obj[item]"><span @click="faC(aa)">{{aa.name}}</span></p>
    </div>
   
  </div>
</div>
</template>

<script>
  import wu from '../zhuye/toubiao'
	
export default {
components:{
		wu,
		// hui
	},
  name: 'hui',
  data () {
    return {
      list:'',
      arr:[],
      Arr:[],
      txt:[],
      name:'el-icon-eleme',
      txt1:'北京',
      obj:[]
    }
  },
  beforeMount(){
  localStorage.biao=this.name
  localStorage.zhu=this.txt1
    },
  methods:{
   faA(){
     localStorage.n=this.list
     
   },
  
   faC(e){
     localStorage.n=e
     this.$router.push({name:'wu',query:{id:e}})

   }
  },
  mounted(){
    this.axios(`http://elm.cangdu.org/v1/cities?type=guess`).then((e)=>{
      this.list=e.data.name
    })
    this.axios(`http://elm.cangdu.org/v1/cities?type=hot`).then((e)=>{
      this.arr=e.data;
    })
     this.axios(`http://elm.cangdu.org/v1/cities?type=group`).then((e)=>{
      this.obj=e.data
       var x=Object.keys(e.data).sort()
       this.Arr=x
       console.log(this.Arr)   
    })

      
     
  }

}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.xun{
  width: 100%;
  height: auto;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-wrap: wrap;
}
.xun>p{
   width: 15%;
   height: 0.5rem;
   border:1px solid #999;
   border-radius: 0.1rem;
   margin-left: 0.1rem;
   text-align: center;
   line-height: 0.5rem;
   overflow: hidden;
}
a{
  text-decoration: none;
}
.xun>p:nth-of-type(1){
  border: 0px;
}
</style>


